@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Anton&family=Cinzel:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.type {
  width: 100%;
  align-content: center;
  background-color: pink;
  text-align: center;
  font-family: "Roboto Slab ExtraLight";
}

.Cards {
  background-color: pink;
  align-items: center;
  align-self: center;
  align-content: center;
  position: center;
  child-align: middle;
}

nav {
  background-color: #ffb6c1;
}

.navbar-brand h2 {
  color: #fff;
  font-size: 1.6em;
}

* {
  margin: 0%;
  padding: 0%;
}
html {
  background: #faebd7; /* fallback for old browsers */
}

.type {
  width: 100%;
  align-content: center;
  background-color: #282c34;
  text-align: center;
  font-family: "Roboto Slab ExtraLight";
}
.headerBg {
  background: #faebd7; /* fallback for old browsers */
}

.Cards {
  background: #faebd7; /* fallback for old browsers */
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  align-items: center;
  align-self: center;
  align-content: center;
  position: center;
}

#header {
  border: rgb(222, 218, 213) solid 0.2px;
  padding: 10px;
  border-radius: 10px;
  font-family: "Amatic SC", cursive;
  font-family: "Anton", sans-serif;
  font-family: "Cinzel", serif;
  width: 60%;
  margin: 10px 20% 0 20%;
  text-align: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.btn-grad {
  background-image: linear-gradient(
    to right,
    #757f9a 0%,
    #d7dde8 51%,
    #757f9a 100%
  );
}
.btn-grad {
  margin: 10px;
  padding: 15px 45px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 20px #eee;
  border-radius: 10px;
  display: block;
}

.btn-grad:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}

nav {
  background-color: #ffb6c1;
}

.navbar-brand span {
  color: #fff;
  font-size: 1.6em;
}

