body {
  background-color: #faebd7;
}

.navbar-brand span {
  font-weight: bold;
  color: #fff;
  text-align: center;
  letter-spacing: 2px;
  text-shadow: 3px 3px 20px #ff99cc, -2px 1px 30px #ff99cc;

  font-size: 1.6em;
}

.rect {
  background-color: steelblue;
  outline: 1px black;
  height: 20px;
  width: 20px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

#title {
  margin-left: 20px;
  margin-top: 20px;
}

#sizeSlider {
  margin-top: 20px;
}

#indication {
  margin-top: 20px;
  margin-left: 20px;
}

#menu1 {
  background-color: #faebd7;
}

#bars {
  padding-top: 60px;
  padding-bottom: 40px;
  background-color: #faebd7;
}

#menu2 {
  background-color: #faebd7;
}

.indicationBox {
  width: 25px;
  height: 25px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
}
h5 {
  color: black;
}
#box1 {
  background-color: white;
}

#box2 {
  background-color: #d9200f;
}

#box3 {
  background-color: #77d65c;
}

.changeButton {
  width: 25px;
  margin-bottom: 5px;
}

